.button {
  display: block;
  padding: 10px 26px;
  text-decoration: none;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
  color: @white;
  border: none;
  background-color: @green;
  outline: inherit;
  cursor: pointer;
  word-break: break-word;

  @media (min-width: @tablet-width) {
    padding: 10px 26px;
  }

  &--index {
    font-size: 14px;
    line-height: 18px;
    padding: 10px 26px;

    @media (min-width: @tablet-width) {
      padding: 17px 26px;
      font-size: 20px;
      line-height: 20px;
    }
  }

  &--form {
    @media (min-width: @tablet-width) {
      padding: 14px 26px;
      font-size: 20px;
      line-height: 20px;
    }
  }

  &:hover,
  &:focus {
    background-color: @green-2;
  }

  &:active {
    background-color: @green-2;
    color: rgba(@white, 30%);
  }
}
